import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
const ResultCard = () => {

const [l,setL] =useState([]);
useEffect(() =>{
    const fetchData = async () =>{

    // setData(JSON.parse(localStorage.getItem("Search"))) 
   setL(JSON.parse(localStorage.getItem("Search"))) 
 
  
    }
    fetchData()
    .catch(console.error)
  
  },[l])
   
  // console.log("movies resultcard",movies)
  return (
<Container>


{
    !l ? <div>data not loaded</div>
    :
    <>
    {
        l.map((obj) =>{
            <h1>{obj.original_title}</h1>
        })
    }
    
    </>
}
    
 
          
    
  
   
   

 
 </Container>
    
//  <div>hi</div> 

  )
}

const Container =styled.div``
export default ResultCard