import styled from 'styled-components'
import { Link,Navigate, useNavigate } from 'react-router-dom'
import { useState,useEffect, useContext } from 'react'
import axios from 'axios'
import MovieContext  from '../showData'

const Search = (props) => {

 const  nav = useNavigate()
 const [movies,setMovies] = useState(null);
 const imgURL ="https://image.tmdb.org/t/p/original/";
 const [data,setData] =useState()
 const [lengths,setLengths] =useState()
 const [name,setName] =useState()
 // console.log("value",props.data);
  
// const length = JSON.parse(localStorage.getItem("length"))
// const search =() =>{
//   setData(JSON.parse(localStorage.getItem("Search"))) 
// }
// setData(JSON.parse(localStorage.getItem("Search"))) 

function handleChange(e)  {
  e.preventDefault();
  const data = e.target.value
  if(data.length>0)  {setName(e.target.value)}
  else{setName(null)}
 // setName(null)
  console.log( "serach resultes",e.target.value)
};

useEffect(() =>{
  const fetchData = async () =>{
  
    axios.get(`https://api.themoviedb.org/3/search/movie?api_key=d03799692be1c26faf0ade18a4205f9f&language=en-US&query=${name}&page=1&include_adult=false`).then((resp) =>{
      console.log("length",name.trim().length)
      setMovies(resp.data.results)
      
    })

  }
  fetchData()
  .catch(console.error)
  
})

// useEffect(() =>{
//   const fetchD = async () =>{
//       setMovies(JSON.parse(localStorage.getItem("Search"))) 

//   }
//   fetchD()
//   .catch(console.error)
// })

  // useEffect(() =>{
    
 
  console.log( "search length useeffect",movies)
  // { console.log( "serach resultes",movies.length)}
  return (
 
    <Container>
    {/* <h4>Search</h4> */}
 
    <NavMenu> 
    <Searchbar>
    <a>
              <img src="/images/search-icon.svg" alt="SEARCH" />
         
              <form >
              <label htmlFor="header-search">
               
             
               <input
                  type="text"
                  id="header-search"
                  placeholder="Search "
                  name="s" 
                  onChange={handleChange}
                  // value={name}

                
               />
                 </label>
              
          
               </form>
            
             
    </a>
    </Searchbar>
    </NavMenu> 
  
    {!movies?  <div> </div>
    :
<>
  <Content>
   
  {
    movies.map((obj) =>
  <hold>
    {/* <div>
{obj.original_title}
   </div> */}
    <Wrap>
          
     <img  data ={obj} onClick={() =>{
               nav("/detail/recommends/" + obj.id)
             }
              } src= {`${imgURL + obj.backdrop_path}`} alt=""/>
  
    </Wrap> 
   <br/>
    <Title>
    {obj.original_title}
   </Title> 
   </hold>
   
 )}
 </Content>
 </>
}

 </Container>
          
  )
   }

const Searchbar =styled.div ``

const hold =styled.div``

const Title =styled.div`

top: 10px;
`
const Nav = styled.nav`
    position: fixed;
    // top: 0;
    left: 0;
    right: 0;
    height: 0;
    height: 70px;
    background-color: #090b13;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 35px;
    letter-spacing: 16px;
    z-index: 3;
`
const NavMenu =styled.div`
align-items: center;
display: flex;
flex-flow: row nowrap;
height: 100%;
justify-content: flex-end;
margin: 0px;
padding: 0px;
position: relative;
margin-right: auto;
margin-left: 25px;

@media (max-width: 68px){
    display: none;
}
a {
  display: flex;
  align-items: center;
  padding: 0 0px;

  img {
    height: 20px;
    min-width: 20px;
    width: 20px;
    z-index: auto;
  }

  span {
    color: rgb(249, 249, 249);
    font-size: 13px;
    letter-spacing: 1.42px;
    line-height: 1.08;
    padding: 2px 0px;
    white-space: nowrap;
    position: relative;

   

    &:before {
      background-color: rgb(249, 249, 249);
      border-radius: 0px 0px 4px 4px;
      bottom: -6px;
      content: "";
      height: 2px;
      left: 0px;
      opacity: 0;
      position: absolute;
      right: 0px;
      transform-origin: left center;
      transform: scaleX(0);
      transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
      visibility: hidden;
      width: auto;
    }
  }
  &:hover {
    span:before {
      transform: scaleX(1);
      visibility: visible;
      opacity: 1 !important;
    }
  }
}

`


const search =styled.div``
const Container = styled.div`
padding: 0 0 26px;
`
const Content = styled.div`
  display: grid;
  grid-gap: 35px;
  gap: 25px;
  
  grid-template-columns: repeat(4, minmax(0, 1fr));
  @media (max-width: 768px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
`;

const Wrap = styled.div`
  padding-top: 56.25%;
  border-radius: 10px;
  box-shadow: rgb(0 0 0 / 69%) 0px 26px 30px -10px,
    rgb(0 0 0 / 73%) 0px 16px 10px -10px;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
  border: 3px solid rgba(249, 249, 249, 0.1);
  img {
    inset: 0px;
    display: block;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    position: absolute;
    transition: opacity 500ms ease-in-out 0s;
    width: 100%;
    z-index: 1;
    top: 0;
  }
  &:hover {
    box-shadow: rgb(0 0 0 / 80%) 0px 40px 58px -16px,
      rgb(0 0 0 / 72%) 0px 30px 22px -10px;
    transform: scale(1.05);
    border-color: rgba(249, 249, 249, 0.8);
  }
`;

export default Search