import styled from 'styled-components'
import { Link,useNavigate } from 'react-router-dom'
import { useState,useEffect, useContext } from 'react'
import axios from 'axios'
import { useLocation } from "react-router-dom"
import { ListContext } from '../context';
// const sampleLocation = useLocation();

const Watchlist = () => {
//   const [path,setPath] =useState()
//   const [state,setState] =useState(true)
//   const sampleLocation = useLocation();
  const [originals,setOriginals] = useState(null);
  const imgURL ="https://image.tmdb.org/t/p/original/"
  const  nav = useNavigate()
  const [link,setLink] =useState();
  const [datas,setDatas]= useState()
  //const [watch,setWatch] =useContext(ListContext)
 // localStorage.clear();
 // localStorage.removeItem("Lists");

  useEffect(() => {
    const data = JSON.parse(localStorage.getItem("Lists"))
     setDatas(data)
      console.log("data from another dimension",datas)
  },[])
 
 //console.log("data from another dimension",watch)
  // setPath(sampleLocation.pathname)
//  if(path == '/originals'){
//    setState(true)

//  }

  return (
    <Container>
    <h4>Originals </h4>
{  !datas?
    
   <></>
    :
     <>
  <Contents>
     
  {
    datas.map((obj) =>
<Hold>
    <Wrap>
     <img  data ={obj} onClick={() =>{
               nav("/detail/originals/" + obj.datas.id)
             }
              } src= {`${imgURL + obj.datas.backdrop_path}`} alt=""/>
           {console.log("id no",obj)}
    </Wrap>  
    <br/>
    <Title>
    {obj.datas.original_title}
   </Title> 
   </Hold>
 )}
 </Contents>
    </>
}
 </Container>
   
          
    
  )
}

const Hold =styled.div``

const Title =styled.div`
top: 10px;
`
const Container = styled.div`
padding: 0 0 26px;
`
const Content = styled.div`
display: grid;
grid-column-gap: 25px;
padding: 0px;
grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
grid-auto-flow: column;
grid-auto-columns: minmax(300px,1fr);
overflow-x: auto;

 @media (max-width: 768px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
}
`
const Wrap = styled.div`
  padding-top: 56.25%;
  border-radius: 10px;
  box-shadow: rgb(0 0 0 / 69%) 0px 26px 30px -10px,
    rgb(0 0 0 / 73%) 0px 16px 10px -10px;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
  border: 3px solid rgba(249, 249, 249, 0.1);
  img {
    inset: 0px;
    display: block;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    position: absolute;
    transition: opacity 500ms ease-in-out 0s;
    width: 100%;
    z-index: 1;
    top: 0;
  }
  &:hover {
    box-shadow: rgb(0 0 0 / 80%) 0px 40px 58px -16px,
      rgb(0 0 0 / 72%) 0px 30px 22px -10px;
    transform: scale(1.05);
    border-color: rgba(249, 249, 249, 0.8);
  }
`;

const Contents = styled.div`
  display: grid;
  grid-gap: 25px;
  gap: 25px;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  @media (max-width: 768px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
`;
export default Watchlist