import React, { useEffect,useState } from 'react';
import styled from 'styled-components';
import {createUserWithEmailAndPassword, signInWithPopup, signOut } from "firebase/auth";
import useGlobalStorage from 'use-global-storage';
import {useDispatch, useSelector} from 'react-redux';
import {useNavigate} from 'react-router-dom';
import {auth, provider} from "../Firebase";
import { selectUserEmail,setUserLoginDetails,selectUserPhoto, selectUserName, setSignOutState } from '../features/user/userSlice';
import { Navigate } from 'react-router-dom';
import axios from 'axios';
import Search from './Search';
 
const Header = (props) => {
  const imgURL ="https://image.tmdb.org/t/p/original/";
  const dispatch   = useDispatch()
  const history    = useNavigate()
  const username   = useSelector(selectUserName)
  const userPhoto  = useSelector(selectUserPhoto)
  const nav =useNavigate()
  const [name, setName] = useState();
  const [search,setSearch] =useState([]);
 

  const useStorage = useGlobalStorage({
    storageOptions: { name: 'test-db' }
  });

//   const [list,setList] =useStorage(
//     () =>

//  "list",
//    JSON.parse(localStorage.getItem("Search"))
    
//   );

  useEffect(() =>{
    auth.onAuthStateChanged(async (user) =>{
      if(user) {
        setUser(user)
        history.push('/home')
      }
    })
  },[username])

  // const 
  const handleAuth = () =>{
    if(!username) {
        signInWithPopup(auth,provider).then((result) =>{
          setUser(result.user);
        }).catch((error) =>{
          alert(error.message);
        })
  } else if (username){
    signOut(auth,provider).then(() =>{
      dispatch(setSignOutState());
      history.push('/');
    })
    // .catch((err) => alert(err.message));
  }
};

  const setUser =(user) =>{
     dispatch(setUserLoginDetails({
       name: user.displayName,
       email: user.email,
       photo: user.photoURL,
     })
     );
  };
  const handleSubmit = (e) => {
    e.preventDefault();

   
    //  setSearch(e.target.value);
}

function handleChange(e)  {
  e.preventDefault();
  const data = e.target.value
  if(data.length>0)  {setName(e.target.value)}
  else{setName(null)}
};

useEffect(() =>{

  axios.get(`https://api.themoviedb.org/3/search/movie?api_key=d03799692be1c26faf0ade18a4205f9f&language=en-US&query=${name}&page=1&include_adult=false`).then((resp) =>{
     console.log("length",name.trim().length)
    //setSearch(resp.data.results)
   
  localStorage.setItem('Search', JSON.stringify(resp.data.results))
  localStorage.setItem('length', JSON.stringify(name.trim().length))

     
 
   })
  
},[name])

// if (name.trim().length !== 0) {
//   console.log('input value is NOT empty');
// } else {
//   localStorage.clear()
// }

  //localStorage.setItem('Search', JSON.stringify(search))
  // console.log("length",name.trim().length)
  return (
  
    <Nav>
        <Logo>
            <img src="/images/logo.svg" alt='' />
        </Logo>    
        {
          !username ?
          <Login onClick={handleAuth}>Login</Login>
         : 
          <>
           {/* { console.log("name",name)} */}
           {/* { console.log("name search",search)} */}
        <NavMenu> 
            < a href="/home">
              <img src="/images/home-icon.svg" alt='HOME' />  
              <span>HOME</span>
            </a> 
           
            {/* <a>
              <img src="/images/search-icon.svg" alt="SEARCH" /> */}
              
              {/* <form onSubmit={handleSubmit}>
              <label htmlFor="header-search">
               
             
               <input
                  type="text"
                  id="header-search"
                  placeholder="Search blog posts"
                  name="s" 
                  onChange={handleChange}
                  value={name}

                
               />
                 </label>
              
          
               </form> */}
             
            {/* </a> */}
            <a>
              <img src="/images/watchlist-icon.svg" alt="WATCHLIST" />
              <span onClick={() =>{nav('/watchlist')}}>WATCHLIST</span>
            </a>
            <a>
              <img src="/images/original-icon.svg" alt="ORIGINALS" />
              <span onClick={() =>{nav('/originals')}}>ORIGINALS</span>
            </a>
            <a>
              <img src="/images/movie-icon.svg" alt="search" />
              <span onClick={() =>{nav('/movies')}}>MOVIES</span>
            </a>
            <a>
              <img src="/images/series-icon.svg" alt="SERIES" />
              <span onClick={() =>{nav('/series')}}>SERIES</span>
            </a>
        </NavMenu>
        <SignOut>
          <UserImg src={userPhoto} alt={username} />
          <DropDown>
            <span onClick={handleAuth}>SignOut</span>
          </DropDown>
        </SignOut>
        </>
        }
       
       {/* <Container>
    
          {  !search ? <div>Data not loaded </div>
        :
      <>
      <Content>
    
    {
       search.map((obj) =>
       <Wrap>
       <img  data ={obj} onClick={() =>{
              nav("/detail/recommends/" + obj.id)
            }
             } src= {`${imgURL + obj.backdrop_path}`} alt=""/>
       </Wrap>  
      )}
      </Content>
      </>
          }
        </Container> */}

      </Nav>
  



  )
}

const Nav = styled.nav`
    position: fixed;
    // top: 0;
    left: 0;
    right: 0;
   height: 0;
    height: 70px;
    background-color: #090b13;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 35px;
    letter-spacing: 16px;
    z-index: 3;
`

const Logo = styled.a`
  padding: 0;
  input: text;
  width: 80px;
  margin-top: 4px;
  max-height: 70px;
  font-size: 0;
  display: inline-block;
  img {
    display: block;
    width: 100%;
  }
`;
 const NavMenu =styled.div`
 align-items: center;
 display: flex;
 flex-flow: row nowrap;
 height: 100%;
 justify-content: flex-end;
 margin: 0px;
 padding: 0px;
 position: relative;
 margin-right: auto;
 margin-left: 25px;

 @media (max-width: 68px){
     display: none;
 }
 a {
   display: flex;
   align-items: center;
   padding: 0 12px;

   img {
     height: 20px;
     min-width: 20px;
     width: 20px;
     z-index: auto;
   }

   span {
     color: rgb(249, 249, 249);
     font-size: 13px;
     letter-spacing: 1.42px;
     line-height: 1.08;
     padding: 2px 0px;
     white-space: nowrap;
     position: relative;

    

     &:before {
       background-color: rgb(249, 249, 249);
       border-radius: 0px 0px 4px 4px;
       bottom: -6px;
       content: "";
       height: 2px;
       left: 0px;
       opacity: 0;
       position: absolute;
       right: 0px;
       transform-origin: left center;
       transform: scaleX(0);
       transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
       visibility: hidden;
       width: auto;
     }
   }
   &:hover {
     span:before {
       transform: scaleX(1);
       visibility: visible;
       opacity: 1 !important;
     }
   }
 }

 `

 const Login =styled.a`
  background-color:rgba(0,0,0,0.6);
  padding: 8px 16px;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  border: 1px solid #f9f9f9;
  border-radius: 4px;
  transistion: all 0.2s ease 0s;
  &:hover{
    background-color: #f9f9f9;
    color: #000;
    border-color: transparent;
  }
 `

 const UserImg =styled.img`
  height: 100%;
  
 `

 const DropDown =styled.div`
    posistion: absolute;
    top: 48px;
    //bottom: 10px;
    right: 0px;
    background: rgb(19,19,19);
    border: 1px solid rgba(151,151,151, 0.34);
    border-radius: 4px;
    box-shadow:rgb(0 0 0 /50%) 0px 0px 18px 0px;
    padding: 10px;
    font-size: 14px;
    letter-spacing: 3px;
    width: 100px;
    opacity: 0;
 `;

 const SignOut =styled.div`
   posistion: relative;
   height: 48px;
   widht: 48px;
   display: flex;
   cursor: pointer;
   align-items: center;
   justify-content: center;

   ${UserImg} {
     border-radius: 50%;
     width:  100%;
     height: 100%;
   }

   &:hover {
     ${DropDown} {
       opacity: 1;
       transition-duration: 1s;
     }
   }


 `

 const Container = styled.div`
padding: 0 0 26px;
`
const Content = styled.div`
display: grid;

grid-column-gap: 25px;
padding: 0px;

grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
grid-auto-flow: column;
grid-auto-columns: minmax(300px,1fr);
overflow-x: auto;

 @media (max-width: 768px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
}
`
const Wrap = styled.div`
  padding-top: 56.25%;
  border-radius: 10px;
  box-shadow: rgb(0 0 0 / 69%) 0px 26px 30px -10px,
    rgb(0 0 0 / 73%) 0px 16px 10px -10px;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
  border: 3px solid rgba(249, 249, 249, 0.1);
  img {
    inset: 0px;
    display: block;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    position: absolute;
    transition: opacity 500ms ease-in-out 0s;
    width: 100%;
    z-index: 1;
    top: 0;
  }
  &:hover {
    box-shadow: rgb(0 0 0 / 80%) 0px 40px 58px -16px,
      rgb(0 0 0 / 72%) 0px 30px 22px -10px;
    transform: scale(1.05);
    border-color: rgba(249, 249, 249, 0.8);
  }
`;
export default Header


